<template>
  <div class="new-user">
      <header>
          <div @click="goback()"><i class="fas fa-chevron-left"></i></div>
          <h3 v-bind:class="{ show: isShow}">新用户专区</h3>
      </header>
      <div class="banner">
          <div class="login-show">
            <p>先领红包再消费，优惠多多</p>
            <div>登陆领取</div>
          </div>
      </div>
      <div class="shoplist">
        <div class="shoplist-title">
          首单最高立减30
        </div>
          <shop-list :list='indexlist'></shop-list>
      </div>
  </div>
</template>
<script>
import api from '@/axios/api.js'
import '@/mock.js'
const shopList = () => import('@/components/shop-list')
export default {
  components: {
    shopList
  },
  data: function () {
    return {
      isShow: false,
      indexlist:[]
    }
  },
  created(){
    api.getlist('/indexlist').then(res => {
        this.indexlist = res.list;
        console.log(res.list)
    })
  },
  mounted() {
    window.onscroll = scroll;
    let a=this;
    function scroll(){
      if (window.scrollY >= 62){
          a.isShow = true;
      }
      else if(window.scrollY < 62){
          a.isShow = false;
      }
    }
  },
  methods: {
    goback: function () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}
</script>

<style scoped>
.new-user{
    background-color: blue;
    height: 100vh;
    width: 100vw;
}
header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 15vw;
    font-size: 4vw;
    line-height: 15vw;
    text-align: center;
    color: white;
    z-index: 2;
}
header div{
    position: absolute;
    top:0;
    left: 0;
    width: 10vw;
    height: 15vw;
    background-color:transparent;
    z-index: 2;
}
header h3{
    width: 100vw;
    height: 15vw;
    background-color: rgb(35, 149, 255);
    opacity: 0;
}
.show{
    opacity: 1;
}
.banner{
    position: relative;
    width: 100vw;
    height: 83vw;
    background-repeat:no-repeat;
    background-size:100vw;
    background-image: url('../assets/elm/img/elm/newuser.png')
}
.login-show{
    width: 64.533333vw;
    height: 23.733333vw;
    position: absolute;
    left: 50%;
    bottom: 9.333333vw;
    transform: translateX(-50%);
}
.login-show p{
    width: 100%;
    height: 8vw;
    font-size: 3.6vw;
    line-height: 8vw;
    text-align: center;
    color: #ffdfb0;
}
.login-show div{
    width: 100%;
    height: 17.2vw;
    background: transparent;
    background-size: 100%;
    color: #7c1004;
    font-size: 6vw;
    line-height: 17.2vw;
    text-align: center;
    border: none;
    background-repeat:no-repeat;
    background-image: url('../assets/elm/img/elm/newuserbutton.png')
}

.shoplist-title{
  width: 100vw;
  height: 10vw;
  background-color: white;
  display: flex;
  -moz-box-align: center;
  -webkit-box-align: center;
  align-items: center;
  justify-content:center;
  font-size: 4vw;
}
.shoplist-title::before{
  display: block;
  content: '';
  margin-right: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
.shoplist-title::after{
  display: block;
  content: '';
  margin-left: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
</style>



